export class SolutionList {
  constructor(id, solution) {
    this.solution = null;
    this.isIndeterminate = false;
    this.element = document.getElementById(id);
    if (!this.element) {
      throw new Error(`Table: no element was found with id '${id}'`);
    }
    this.list = document.createElement("div");
    this.list.classList.add("solution");
    this.element.append(this.list);
    this.setSolution(solution);
  }
  createH3(text) {
    let h3 = document.createElement("h3");
    let textElement = document.createTextNode(text);
    h3.append(textElement);
    h3.classList.add("green");
    return h3;
  }
  createH4(text) {
    let h4 = document.createElement("h4");
    let textElement = document.createTextNode(text);
    h4.append(textElement);
    return h4;
  }
  createList(content) {
    let ul = document.createElement("ul");
    content.map((value) => {
      let li = document.createElement("li");
      let text = document.createTextNode(`${value.variable}: ${value.value}`);
      li.append(text);
      ul.append(li);
    });
    return ul;
  }
  setSolution(solution) {
    this.solution = solution;
    this.list.innerText = "";
    if (this.solution) {
      this.render();
    }
  }
  setIsIndeterminate(isIndeterminate) {
    this.isIndeterminate = isIndeterminate;
  }
  render() {
    this.list.innerText = "";
    this.list.append(this.createH3(`Z = ${this.solution.max}`));
    if (this.isIndeterminate)
      this.list.append(this.createH4("Solução Indeterminada"));
    this.list.append(this.createH4("Variáveis Básicas"));
    this.list.append(this.createList(this.solution.basic));
    this.list.append(this.createH4("Variáveis Não Básicas"));
    this.list.append(this.createList(this.solution.notBasic));
  }
}
